.App {
  font-family: 'Outfit', sans-serif;
  text-align: center;
  background-color: hsl(212, 45%, 89%);
  height: 100vh;
  padding: 30px;
  max-width: 1440px;
  
}

.container {
  background-color: #fff;
  padding: 30px;
  border-radius: 20px;
  max-width: 370px;
  display: flex;
  flex-direction: column;
  margin: auto;
}

#qr {
  border-radius: 20px;
  max-width: 350px;
}

h2, p {
  font-weight: 700;
  font-size: 1.75rem;
  text-align: center;
  padding: 15px;
  color: hsl(218, 44%, 22%);
  margin-bottom: 10px;
}

p {
  font-weight: 400;
  font-size: 15px;
  color: hsl(220, 15%, 55%);
  line-height: 1.5rem;
}

@media screen and (max-width: 375px) {
  .container {
    width: 370px;
    display: flex;
    flex-direction: column;
    margin: auto;
  }
}

.attribution { font-size: 11px; text-align: center; }
.attribution a { color: hsl(228, 45%, 44%); }